.HeaderComponent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 14vh; /* TitleBar 10vh + NavBar 4vh*/
  top: 0;
  z-index: 2;
  position: fixed;
}

.TitleBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: rgba(255, 255, 255, 1.0);
  padding: auto 0 auto 0;
  height: 10vh;
  background-color: rgb(8, 8, 8);
  z-index: inherit;
}

.TitleBar h1 {
  margin: 0;
  padding: 0;
  font-size: 1.6em;
  font-weight: 700;
}

.Hline {
  display: none;
  z-index: inherit;
}

@media screen and (max-width: 500px) {
  .HeaderComponent {
    height: 17vh;
  }

  #TopMenu .NavBar {
    height: 7vh;
  }

  #TopMenu {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  #TopMenu.gone {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
    z-index: -1;
  }

  .Hline {
    display: block;
    width: 92%;
    margin: 0 auto 0 auto;
    border-top: 3px solid white;
  }
}



