.Map {
  width: 100%;
  height: 80%;
}

@media screen and (max-width:791px) {
  .Map {
    width: 420px;
    margin: 0 auto 0 auto;
  }
}

@media screen and (max-width:540px) {
  .Map {
    width: 90%;
    margin: 0 auto 0 auto;
  }
}



