img {
  opacity: 0;
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

img.loaded {
  opacity: 1.0;
}

.imagePlaceholder {
  display: -ms-flexbox;
  display: flex;
  margin: 1vmin;
  background: -webkit-linear-gradient(105deg, rgba(50, 50, 50, 0.5), rgba(150, 150, 150, 0.5));
  background: -o-linear-gradient(105deg, rgba(50, 50, 50, 0.5), rgba(150, 150, 150, 0.5));
  background: linear-gradient(-15deg, rgba(50, 50, 50, 0.5), rgba(150, 150, 150, 0.5));
}



