.FooterComponent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 14vh;
  bottom: 0;
  z-index: 2;
  position: fixed;
}

.Spacer {
  min-width: 100%;
  height: 10vh; /* same as TitleBar */
  background-color: rgb(8, 8, 8);
  z-index: inherit;
}

.Hline {
  display: none;
  z-index: inherit;
}

@media screen and (max-width: 500px) {
  .Hline {
    display: block;
    width: 92%;
    margin: 0 auto 0 auto;
    border-top: 3px solid white;
  }

  #BottomMenu {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  #BottomMenu.gone {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: translateY(100%);
        -ms-transform: translateY(100%);
            transform: translateY(100%);
    z-index: -1;
  }
}



